import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./slickcustom.css";
import { callPopularContentAPI } from "../../Actions/HomeAction";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import moment from "moment";

const PopularContentCarousel = (props) => {

  var settings = {
    // dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,
  };
  return (
    <div className="container">
      <Slider {...settings}>
        {props.popularContent &&
          props.popularContent.slice(0, 8).map((content, index) => {
            let data = content.supertopicContent.slice(0, 500);
            console.log("data", data);
            return (
              <div key={content.supertopicId} className="px-3">
                <h3>{content.supertopicName}</h3>
                <div className="content-info col-12">
                  <div className="row">
                    <div className="col-6 p-0">
                      <span>Blog</span>
                    </div>
                    <div className="col-6 p-0 text-right">
                      <span>{moment(content.publishedAt).format("MM-DD-YYYY")}</span>
                    </div>
                    <div className="col-12 p-0 mt-4">
                      <div dangerouslySetInnerHTML={{ __html: content.supertopicContent }}></div>
                    </div>
                    <a href="#">Read More</a>
                  </div>
                </div>
              </div>
            )
          })}
      </Slider>
    </div>
  );
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      callPopularContentAPIAction: callPopularContentAPI,
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(PopularContentCarousel);
