import {
  ABOUT_YOU,
  COUNTRY_SELECTION,
  EMPLOYEE_INFO,
  ADD_USER,
  ADD_USER_ID
} from "../ReducerAction/type";

const initialState = {
  isOpen: "",
  about_you: false,
  country_selection:false,
  employee_info:false,
  add_user:false,
  add_user_id: ""
};

const timelineReducer = (state = initialState, action) => {
  // console.log("action", action);
  switch (action.type) {
    case ABOUT_YOU:
      return {
        ...state,
        about_you:action.status,
      };
      break;
    case COUNTRY_SELECTION:
      return {
        ...state,
        country_selection:action.status,
      };
      break;
    case EMPLOYEE_INFO:
      return {
        ...state,
        employee_info:action.status,
      };
      break;
    case ADD_USER:
      return {
        ...state,
        add_user:action.status,
      };
      break;
      case ADD_USER_ID:
        return {
          ...state,
          add_user_id:action.status,
        };
        break;
    default:
      return state;
  }
};

export default timelineReducer;
