import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import country_img from "../../Scss_Homepages/images/Hero-Image-askan.jpg";
import "./slickcustom.css";
import {
  callGetPopularCountriesAPI,
  callGetSuperTopicsListAPI,
} from "../../Actions/HomeAction";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
const PopularCountryCarousel = (props) => {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3,
  };
  return (
    <div className="container">
      <Slider {...settings}>
        {props.popularCountries &&
          props.popularCountries.map((item, index) => {
            return (
              <div className="px-3" key={index}>
                <div className="country-card">
                  <div className="country-image-wrapper">
                    <h3>{item.countryName}</h3>
                  </div>
                  <div className="country-image">
                    <img src={country_img} />
                  </div>
                  <div className="details-wrap">
                    <span>
                      <div dangerouslySetInnerHTML={{ __html: item.supertopicContent }}></div>
                    </span>
                  </div>
                </div>
              </div>
            );
          })}
      </Slider>
    </div>
  );
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      callGetSuperTopicsListAPIAction: callGetSuperTopicsListAPI,
      callGetPopularCountriesAPIAction: callGetPopularCountriesAPI,
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(PopularCountryCarousel);
