import React, { useEffect, useState } from "react";
import "../../Scss_Homepages/css/bootstrap-grid.css";
import "../../Scss_Homepages/css/bootstrap-reboot.css";
import "../../Scss_Homepages/css/bootstrap.css";
import "../../Scss_Homepages/css/custome.css";
import Card, { CardLoader } from "./Card";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import PopularCountryCarousel from "./PopularCountryCarousel";
import Pulse from "./Pulse";
import PopularContentCarousel from "./PopularContentCarousel";
import {
  callgetAlertsAPI,
  callgetBlogArticleWPAPI,
  callgetBlogsforCountryAPI,
  callGetCountryListAPI,
  callGetExpertAPI,
  callGetExpertDetailsAPI,
  callGetPopularCountriesAPI,
  callGetPopularCountryListAPI,
  callGetSolutionsAPI,
  callGetSuperTopicsListAPI,
  callPopularContentAPI
} from "../../Actions/HomeAction";
import { getUserProfile } from "../../utils/storage";
import HeaderText from "./HeaderText";

const expertSolutionName = "Consulting - Ask an Expert - Expert Profile"
const expertSolutionCount = 1
const blogSolutionCount = 1

const Home = (props) => {

  const [countryName, setCountryName] = useState([]);
  const [id, setId] = useState("");
  const [countryList, setCountryList] = useState([]);
  const [CountryID, setCountryIds] = useState([]);
  const [popularCountryList, setPopularCountryList] = useState([])
  const [popularContent, setPopularContent] = useState([])
  const [data, setData] = useState({});
  const [blogSolutions, setBlogSolutions] = useState([])
  const [expertSolutions, setExpertSolutions] = useState([])
  const [countriesLoading, setCountriesLoading] = useState(true)
  const [userData, setUserData] = useState()

  const solutionName = "Popular Countries Template"

  useEffect(() => {
    var user_data = getUserProfile();
    setUserData(user_data)
    getCountryList(user_data && user_data.userId ? user_data.userId : "");
    if (CountryID) {
      setData({
        ...data,
        CountryIds: CountryID,
        solutionName: solutionName,
      });
    }
    getPopularCountryList()
    getPopularContentList()
    blogAndExpertSolutionIds()
  }, []);

  const getCountryList = (userID) => {
    props
      .callgetCountryListAPIAction(userID)
      .then((response) => {
        setCountryList(response.data.data);
        let temp = response.data.data.map((x) => {
          return x.id;
        });
        setId(temp);
        setCountryIds(temp.toString().split(","));
        let request = response.data.data.map((item) => {
          return item.country_Name;
        });
        console.log("request",request);
        setCountryName(request);
        setCountriesLoading(false)
      })
      .catch((errors) => { });
  };

  const getPopularCountryList = async () => {
    let popularCountryData = await props.callGetPopularCountriesAPIAction()
    setPopularCountryList(popularCountryData?.data?.data || [])
  };

  const getPopularContentList = async () => {
    let popularContentData = await props.callPopularContentAPIAction()
    setPopularContent(popularContentData?.data?.data || [])
  }

  const blogAndExpertSolutionIds = async () => {
    let [blogSolution, expertSolution] = await Promise.all([
      props.callgetBlogArticleWPAPIAction(blogSolutionCount),
      props.callGetSolutionsAPIAction(expertSolutionCount, {
        solutionName: [expertSolutionName]
      })
    ])
    setBlogSolutions(blogSolution?.data?.data || [])
    setExpertSolutions(expertSolution?.data?.data || [])
  }

  return (
    <div>
      <div className="container-fluid">
        <HeaderText user={userData} />
        <div className="row">
          {countriesLoading ?
            [1, 2, 3, 4, 5, 6].map(item => (
              <CardLoader />
            ))
            :
            countryList &&
            countryList.map((item, index) => {
              return (
                <Card
                  key={index}
                  id={id}
                  // expertID={expertID}
                  countryName={countryName}
                  name={item.country_Name}
                  country={item}
                  className="border-maroon"
                  bullet="maroon_bg"
                  blogCount={2}
                  expertSolutions={expertSolutions}
                  blogSolutions={blogSolutions}
                />
              );
            })}
        </div>

        <div className="row">
          <div className="country-action-wrapper">
            <div className="row">
              <div className="col-md-6">
                <a href="#" className="btn btn-primary">
                  Compare Countries
                </a>
              </div>
              <div className="col-md-6 text-right action-text">
                <span>Expert Hours Balance : 18.5hrs</span>
                <a href="#">Buy More</a>
                <span>I</span>
                <a href="#">Add Country</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      {popularCountryList.length ?
        <>
          <hr className="mt-5 mb-5" />
          <div className="container-fluid">
            <div className="row">
              <div className="header_wrapper col-12 mt-3 mb-2 pr-0">
                <h2 className="ml-3">Popular Countries</h2>
              </div>
              <div className="popular-card">
                <div>
                  <PopularCountryCarousel popularCountries={popularCountryList} />
                </div>
              </div>
            </div>
          </div>
        </>
        : null}
      <hr className="mt-3 mb-5" />
      <Pulse />
      {popularContent ?
        <>
          <hr className="mt-5 mb-5" />
          <div className="container-fluid">
            <div className="row">
              <div className="header_wrapper col-12 mt-3 mb-2 pr-0">
                <h2>Popular Content</h2>
              </div>
            </div>
            <div className="row">
              <div className="popular-content">
                <div>
                  <PopularContentCarousel
                    popularContent={popularContent}
                  />
                </div>
              </div>
            </div>
            <div className="row">
              <div className="btn-bottom-wrap mt-5 mb-5">
                <a href="#" className="btn btn-primary">
                  <ion-icon
                    name="chatbubble-ellipses-outline"
                    className="chatbox"
                  />
                  Ask an Expert
            </a>
              </div>
            </div>
          </div>
        </>
        : null}
    </div>
  );
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      callgetCountryListAPIAction: callGetCountryListAPI,
      callGetPopularCountryListAPIAction: callGetPopularCountryListAPI,
      callGetSuperTopicsListAPIAction: callGetSuperTopicsListAPI,
      callGetExpertDetailsAPIAction: callGetExpertDetailsAPI,
      callgetBlogArticleWPAPIAction: callgetBlogArticleWPAPI,
      callgetBlogsforCountryAPIAction: callgetBlogsforCountryAPI,
      callgetAlertsAPIAction: callgetAlertsAPI,
      callGetExpertAPIAction: callGetExpertAPI,
      callGetPopularCountriesAPIAction: callGetPopularCountriesAPI,
      callPopularContentAPIAction: callPopularContentAPI,
      callGetSolutionsAPIAction: callGetSolutionsAPI
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(Home);
