import React, { useEffect } from 'react';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import "./Timeline.css";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { aboutYouAction } from "../../ReducerAction/TimelineAction";
import { getAboutYou } from '../../utils/storage';

 function BasicTimeline(props) {
  useEffect(() =>{
    // props.aboutYouAction();
    let about_you = getAboutYou();
    console.log("about_you", about_you);
  },[])
  return (
    <Timeline className="custom-Timeline_main">
      <TimelineItem className={props.timeline && props.timeline.about_you ? "Custom-lineItem timeline-active":"Custom-lineItem"}>
        <TimelineContent className="Custom-timeline">About You</TimelineContent>
        <TimelineSeparator className="Separator-custom">
          <TimelineDot className="Custom_Dot"/>
          <TimelineConnector />
        </TimelineSeparator>
      </TimelineItem>
      <TimelineItem className={props.timeline && props.timeline.country_selection ? "Custom-lineItem timeline-active":"Custom-lineItem"}>
        <TimelineContent className="Custom-timeline">Country Selection</TimelineContent>
        <TimelineSeparator className="Separator-custom">
          <TimelineDot className="Custom_Dot"/>
          <TimelineConnector />
        </TimelineSeparator>
      </TimelineItem>
      <TimelineItem className={props.timeline && props.timeline.employee_info ? "Custom-lineItem timeline-active":"Custom-lineItem"}>
        <TimelineContent className="Custom-timeline">Employee Info</TimelineContent>
        <TimelineSeparator className="Separator-custom">
          <TimelineDot className="Custom_Dot"/>
          <TimelineConnector className="Custom_Connector"/>
        </TimelineSeparator>
      </TimelineItem>
      <TimelineItem className={props.timeline && props.timeline.add_user ? "Custom-lineItem timeline-active":"Custom-lineItem"}>
        <TimelineContent className="Custom-timeline">Add Users</TimelineContent>
        <TimelineSeparator className="Separator-custom">
          <TimelineDot className="Custom_Dot"/>
        </TimelineSeparator>
      </TimelineItem>
    </Timeline>
  );
}
const mapStateToProps = (state) => {
  return {
    timeline: state.timeline,
  };
};


const mapDispatchToProps = (dispatch) =>
bindActionCreators(
  {
    aboutYouAction: aboutYouAction,
  
  },
  dispatch
);

export default connect(mapStateToProps, mapDispatchToProps)(BasicTimeline);