import React, { useEffect } from "react";
import { Picky } from "react-picky";
import "react-picky/dist/picky.css";
import "./MySelect.css";
import { options } from "./makeData";
const MySelect = ({
  data,
  labelKey,
  valueKey,
  updateDropdown,
  value,
  placeholder,
}) => {
  return (
    <div>
      <div className="">
        <div className="">
          <Picky
            options={data}
            labelKey={labelKey}
            valueKey={valueKey}
            multiple={true}
            placeholder={placeholder}
            // includeFilter
            includeSelectAll
            value={value}
            onChange={updateDropdown}
            renderSelectAll={({
              // filtered,
              tabIndex,
              allSelected,
              toggleSelectAll,
              multiple,
            }) => {
              // Don't show if single select or items have been filtered.
              if (multiple) {
                return (
                  <div
                    tabIndex={tabIndex}
                    role="option"
                    className={allSelected ? "option" : "option"}
                    onClick={toggleSelectAll}
                    onKeyPress={toggleSelectAll}
                  >
                    <span className="ml-0">
                      <input
                        type="checkbox"
                        checked={value && data.length === value.length}
                      />
                      All
                    </span>
                  </div>
                );
              }
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default MySelect;
