import React, { useState, useEffect } from "react";
import TextFieldComponent from "../../Components/TextFieldComponent/TextFieldComponent";
import {
  callgetUserProfileAPI,
  callUpdateUserProfileAPI,
  callUploadProfilePicAPI,
} from "../../Actions/UserProfileAction";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import Camera_Img from "../../assets/images/camera.png";
import { getKeyClockToken_Data } from "../../utils/storage/index";
import jwt_decode from "jwt-decode";
import constants from "../../utils/constants";
import { Link, useHistory } from "react-router-dom";
import {
  aboutYouAction,
  storeUserId,
} from "../../ReducerAction/TimelineAction";
import { CustomeNotification } from "../../Components/CustomeNotification/CustomeNotification";
import UserRegistrationSidebar from "../../Components/UserRegistrationLayout/UserRegistrationSidebar";
import UserService from "../../services/UserService";
import { storeUserProfile } from "../../utils/storage/index";
const UserProfile = (props) => {
  const history = useHistory();
  const hiddenFileInput = React.useRef(null);
  const [showErr, setError] = useState("");
  const [isError, setIsError] = useState({});
  const [isFocus, setIsFocus] = useState({});
  const [ImageUrl, setImageURL] = useState("");
  const [requestParams, setRequestParams] = useState({});
  const [initialValues, setInitialValues] = useState({
    firstName: "",
    lastName: "",
    emailId: "",
    jobTitle: "",
    companyName: "",
    filePathUri: "",
    preferredName: "",
  });

  requestParams.emailId = initialValues.emailId;
  requestParams.companyName = initialValues.companyName;
  requestParams.jobTitle = initialValues.jobTitle;
  requestParams.ImageUrl = ImageUrl;
  requestParams.firstName = initialValues.firstName;
  requestParams.lastName = initialValues.lastName;
  requestParams.preferredName = initialValues.preferredName;


  useEffect(() => {
    var token_data = getKeyClockToken_Data();
    var newData = token_data ? jwt_decode(token_data) : "";
    var kayClock_id = newData ? (newData.sub ? newData.sub : "") : "";
    if (token_data) {
      getUserDetails(kayClock_id);
    } else {
      UserService.initKeycloak(UserProfile);
    }
  }, []);

  const getUserDetails = (kayClock_id) => {
    props
      .callgetUserProfileAPIAction(kayClock_id)
      .then((response) => {
        console.log("user profile", response.data.data);
        setInitialValues({
          ...initialValues,
          emailId: response.data.data.emailId,
          companyName: response.data.data.companyName,
          firstName: response.data.data.userProfile.firstName,
          lastName: response.data.data.userProfile.lastName,
          jobTitle: response.data.data.userProfile.jobTitle,
        });
        setImageURL(response.data.data.userProfile.imageUrl);
        storeUserProfile(response.data.data.userProfile);
        setRequestParams(response.data.data.userProfile);
      })
      .catch((errors) => {
        console.log("error", errors);
      });
  };

  const handleChangeValue = (e) => {
    setInitialValues({
      ...initialValues,
      [e.target.name]: e.target.value,
    });
    setIsError({ ...isError, [e.target.name]: "" });
  };

  const validate = (values) => {
    let errors = {};
    return errors;
  };

  const handleFocus = (e) => {
    const validation = validate(initialValues);
    setIsError(validation);
    setIsFocus({ ...isFocus, [e.target.name]: true });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const validation = validate(initialValues);
    setIsError(validation);

    props
      .callUpdateUserProfileAPIAction(requestParams.id, requestParams)
      .then((res) => {
        console.log("profile update", res);
        history.push(constants.ROUTE.COUNTRY.COUNTRY);
        CustomeNotification(
          "success",
          "Profile Updated Successfully",
          "Success",
          2000
        );
        props.aboutYouAction(true);
      })
      .catch((error) => {
        if (error?.response?.data?.errors) {
          let result = error.response.data
            ? error.response.data.errors
              ? error.response.data.errors
              : ""
            : "";
          if (result != "") {
            setError(result);

            CustomeNotification(
              "error",
              error.response.data.errors,
              "Error",
              2500,
              () => {}
            );
          }
        }
      });
  };

  const handleChange = (event) => {
    const data = new FormData();
    data.append("file", event.target.files[0]);
    props
      .callUploadProfilePicAPIAction(data)
      .then((response) => {
        console.log("file upload", response.data.data);
        setImageURL(response.data.data.id);
      })
      .catch((errors) => {});
  };
  const handleClick = (event) => {
    hiddenFileInput.current.click();
  };
  const handlePrevious = () => {
    props.aboutYouAction(false);
  };
  return UserService.initKeycloak ? (
    UserService.isLoggedIn ? (
      <>
        <div className="main_wrapper position-relative">
          <UserRegistrationSidebar />
          <div className="container">
            <div className="row justify-content-center mt-3">
              <div className="col-8">
                <div className="row"></div>
                <div className="col-12">
                  <div className="row">
                    <div className="header-section">
                      <h2>
                        Welcome,{" "}
                        {initialValues.firstName
                          ? initialValues.firstName
                          : "Guest"}
                        !{" "}
                      </h2>
                      <h2>
                        You’re steps away from getting started. Let’s begin!
                      </h2>
                    </div>
                  </div>
                  <div className="row">
                    <div className="form">
                      <div className="row">
                        <div className="col-4">
                          <div className="floating">
                            <TextFieldComponent
                              id="firstName"
                              label="First name*"
                              data-test="firstName"
                              dataContent="First name*"
                              type="text"
                              placeholder="First name*"
                              name="firstName"
                              value={initialValues.firstName}
                              onBlur={handleFocus}
                              onChange={handleChangeValue}
                            />
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="floating">
                            <TextFieldComponent
                              id="lastName"
                              label="Last Name*"
                              data-test="lastName"
                              dataContent="Last Name*"
                              type="text"
                              placeholder="Last Name*"
                              name="lastName"
                              value={initialValues.lastName}
                              onBlur={handleFocus}
                              onChange={handleChangeValue}
                            />
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="floating">
                            <TextFieldComponent
                              id="preferredName"
                              label="Preferred Name*"
                              data-test="preferredName"
                              dataContent="Preferred Name*"
                              type="text"
                              placeholder="Preferred Name*"
                              name="preferredName"
                              value={initialValues.preferredName}
                              onBlur={handleFocus}
                              onChange={handleChangeValue}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-12 px-0">
                      <div className="row">
                        <div className="col-6">
                          <div className="floating">
                            <TextFieldComponent
                              id="input__password"
                              label="Email Address*"
                              data-test="input__password"
                              dataContent="Email Address*"
                              type="email"
                              name="emailId"
                              placeholder="Email Address*"
                              value={initialValues.emailId}
                              onBlur={handleFocus}
                              onChange={handleChangeValue}
                            />
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="floating">
                            <TextFieldComponent
                              id="Job_Title"
                              label="Job Title"
                              data-test="Job_Title"
                              dataContent="Job Title"
                              type="text"
                              name="jobTitle"
                              placeholder="Job Title"
                              value={initialValues.jobTitle}
                              onBlur={handleFocus}
                              onChange={handleChangeValue}
                            />
                          </div>
                        </div>
                      </div>
                      <div className="col-12 px-0">
                        <div className="row">
                          <div className="col-6">
                            <div className="floating">
                              <TextFieldComponent
                                id="Company_Name"
                                label="Company Name*"
                                data-test="Company_Name"
                                dataContent="Company Name*"
                                type="text"
                                placeholder="Company Name*"
                                name="companyName"
                                readOnly
                                value={initialValues.companyName}
                                onBlur={handleFocus}
                                onChange={handleChangeValue}
                              />
                            </div>
                          </div>
                          <div className="col-6">
                            <div className="floating">
                              <TextFieldComponent
                                id="industry"
                                label="Industry*"
                                data-test="industry"
                                dataContent="Industry*"
                                type="text"
                                readOnly
                                placeholder="Industry*"
                                name="industry"
                                value={initialValues.industry}
                                onBlur={handleFocus}
                                onChange={handleChangeValue}
                              />
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="floating">
                        <div className="profile-info" data-test="profile-info">
                          <div className="profile-picture d-flex align-items-center">
                            <div className="profile">
                              <img
                                src={
                                  ImageUrl
                                    ? `${constants.API.COUNTRY.GET_FLAG_DOWNLOAD}${ImageUrl} `
                                    : Camera_Img
                                }
                                className="img-fluid"
                              />
                            </div>
                            <button
                              className="mt-0 ml-4 btn btn-secondary"
                              onClick={handleClick}
                            >
                              Change
                            </button>
                          </div>
                          <input
                            type="file"
                            style={{ display: "none" }}
                            ref={hiddenFileInput}
                            accept=".webm,.png, .mp4, .jpg, .jpeg, .gif, .docx, .doc "
                            onChange={handleChange}
                          />
                          <a className="upload_img">Remove</a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="errormsg">{showErr ? showErr : ""}</div>
              </div>

              <div className="col-8">
                <div className="action-wrap col-12">
                  <div className="row align-items-center justify-content-between">
                    <div data-test="skip_link">
                      <Link to={constants.ROUTE.COUNTRY.COUNTRY}>
                        Skip for now
                      </Link>
                    </div>
                    <div>
                      <a
                        href="#"
                        type="button"
                        onClick={handleSubmit}
                        className="btn btn-primary mt-0 "
                      >
                        Next
                        <i className="ph-arrow-right" />
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    ) : (
      ""
    )
  ) : (
    ""
  );
};

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      aboutYouAction: aboutYouAction,
      storeUserId: storeUserId,
      callgetUserProfileAPIAction: callgetUserProfileAPI,
      callUpdateUserProfileAPIAction: callUpdateUserProfileAPI,
      callUploadProfilePicAPIAction: callUploadProfilePicAPI,
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(UserProfile);
