import React, { useEffect, useState } from "react";
import IndiaFlag from "../../assets/images/Flag-India.jpg";
import map_pin from "../../assets/images/map-pin.svg";
import mappin_active from "../../assets/images/map-pin-active.svg";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  callAddCountryAPI,
  callgetCountryListAPI,
  callgetFavouriteCountryAPI,
  callgetRegionListAPI,
  callgetSpecificRegionListAPI,
} from "../../Actions/CountryAction";
import { CustomeNotification } from "../../Components/CustomeNotification/CustomeNotification";
import { Link, useHistory } from "react-router-dom";
import constants from "../../utils/constants";
import { getUserProfile, storeCountryData } from "../../utils/storage";
import UserRegistrationSidebar from "../../Components/UserRegistrationLayout/UserRegistrationSidebar";
import { countrySelectionAction } from "../../ReducerAction/TimelineAction";
import UserService from "../../services/UserService";
import MySelect from "../../Components/MultiselectDropDown/MySelect";
const Country = (props) => {
  const [countryList, setCountryList] = useState([]);
  const [regionList, setRegionList] = useState([]);
  const [favouriteList, setFavourite] = useState([]);
  const [specificRegionList, setSpecificRegionList] = useState([]);
  const [countryId, setCountryId] = useState("");
  const [searchItem, setSearchItem] = useState("");
  const [userID, setUserId] = useState("");
  const [mapData, setMapData] = useState([]);
  const history = useHistory();
  const [id, setId] = useState(1);

  useEffect(() => {
    var user_data = getUserProfile();
    setUserId(user_data.userId);
    getCountryList();
    getRegionList();
    getFavouriteCountryList(user_data.userId);
  }, []);

  useEffect(() => {
    const result = countryList.filter((country) =>
      country.country_Name.toLowerCase().includes(searchItem)
    );

    setCountryList(result);
  }, [searchItem]);

  const getCountryList = () => {
    props
      .callgetCountryListAPIAction()
      .then((response) => {
        console.log("country List", response);
        setCountryList(response.data.data);
        setCountryId(response.data.data[0].id);
        let FlagData = [];
        response.data.data.forEach((item, index) => {
          console.log("item", item);
          FlagData[item.id] = item;
        });
        console.log("userID", userID);
      })
      .catch((errors) => {});
  };

  const getRegionList = () => {
    props
      .callgetRegionListAPIAction()
      .then((response) => {
        console.log("region List", response);
        setRegionList(response.data.data);
      })
      .catch((errors) => {});
  };

  const handleClick = (id) => {
    console.log("selected id", id);
    setId(id.id);
    countryList.value = id;
    // let tempData = [...countryList]
    // const index = tempData.findIndex((x) => x.id === id.id)
    // tempData.value = id.id;
    // console.log("tempData",tempData);
    // setCountryList(tempData);
    props
      .callgetSpecificRegionListAPIAction(id[0].id)
      .then((response) => {
        console.log("specific region List", response.data.data);
        setSpecificRegionList(response.data.data);
      })
      .catch((errors) => {});
  };

  const handleSubmit = () => {
    let data = countryList.filter((item) => mapData.includes(item.id));
    var countryIds = data.map((item) => {
      return item.id;
    });
    let countryId = countryIds.toString();
    props
      .callAddCountryAPIAction({ userid: userID, countryId })
      .then((response) => {
        console.log("add country", response);
        history.push(constants.ROUTE.LOCATION.LOCATION);
        CustomeNotification(
          "success",
          "Country Added to Favourite List",
          "Success",
          2000
        );
        props.countrySelectionAction(true);
        storeCountryData(
          userID,
          countryList.filter((x) => x.isMap)
        );
      })
      .catch((error) => {
        if (error?.response?.data?.errors) {
          let result = error.response.data
            ? error.response.data.errors
              ? error.response.data.errors
              : ""
            : "";
          if (result != "") {
            CustomeNotification(
              "error",
              error.response.data.errors,
              "Error",
              2500,
              () => {}
            );
          }
        }
      });
  };
  const getFavouriteCountryList = (UserID) => {
    props
      .callgetFavouriteCountryAPIAction(UserID)
      .then((response) => {
        console.log(
          "favourite country List",
          response.data.data.employeeCountryCount
        );
        let favouriteData = {};
        response.data.data.employeeCountryCount.forEach((item) => {
          favouriteData[item.countryID] = item;
        });
        console.log("favouriteData action", favouriteData);
        setFavourite(favouriteData);
      })
      .catch((errors) => {});
  };

  const handleSearch = (e) => {
    setSearchItem(e.target.value);
  };

  const handleChangeMap = (data, id) => {
    let tempData = [...data];
    if (tempData.includes(id)) {
      tempData.pop(id);
    } else {
      tempData.push(id);
    }
    setMapData(tempData);
    let tempData1 = [...countryList];
    let index = tempData1.findIndex((item) => item.id === id);
    tempData1[index] = {
      ...tempData1[index],
      // isMap: tempData1[index].isMap ? false : true,
    };
    setCountryList(tempData1);
  };

  const updateDropdown = (selected, item) => {
    console.log("selected", selected, item);
  };
  return (
    <div className="main_wrapper position-relative">
      <UserRegistrationSidebar />
      <div className="container">
        <div className="row justify-content-center mt-3">
          <div className="col-8">
            <div className="row">
              <div className="col-6">
                <a href="#" className="back-btn">
                  Previous Step
                </a>
              </div>
            </div>
            <div className="col-12">
              <div className="row">
                <div className="header-section">
                  <h2>
                    Select countries your company has operations in or is
                    interested in.
                  </h2>
                  <h4>
                    You will see these countries/states on your Home Page every
                    time you log in.
                  </h4>
                </div>
              </div>
              <div className="row mb-4">
                <div className="col-6 pl-0">
                  <div className="search-wrap">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Search By Country"
                      value={searchItem}
                      onChange={handleSearch}
                    />
                    <a href="#">
                      <i className="ph-magnifying-glass" />
                    </a>
                  </div>
                </div>

                <div className="col-6 pr-0">
                  <div className="country_dropdown">
                    <MySelect
                      data={regionList}
                      labelKey="region_Name"
                      placeholder="Find countries by region"
                      valueKey="id"
                      value={countryList.value || []}
                      updateDropdown={(selected) => handleClick(selected)}
                    />
                    {/* <input
                    type="text"
                    className="form-control"
                    placeholder="Search By Country"
                    value={searchItem}
                    onChange={handleSearch}
                  />
                  <a href="#">
                    <i className="ph-magnifying-glass" />
                  </a> */}
                  </div>
                </div>
              </div>
            </div>

            {/* <div className="filter-navigation">
              <span>By region</span>
              <button
                href="#"
                className={` ${id === 1 ? "active" : ""}`}
                onClick={() => handleClick(1)}
              >
                All Countries
              </button>
              {regionList.slice(0, 5).map((item, index) => {
                return (
                  <button
                    href="#"
                    key={index}
                    className={` ${id === item.id ? "active" : ""}`}
                    onClick={() => handleClick(item.id)}
                  >
                    {item.region_Name}
                  </button>
                );
              })}
            </div> */}

            <div className="col-8 mx-auto">
              {id === 1 ? (
                <>
                  <div className="row">
                    <div className="country-wrapper col-12">
                      {countryList.map((item, index) => {
                        const favouriteCountry = favouriteList[item.id];
                        console.log("favouriteCountry", favouriteCountry);
                        return (
                          <div className="row" key={index}>
                            <div className="country-list">
                              <a href="#">
                                <span className="country-list-active">
                                  {item.country_Name}
                                </span>
                              </a>
                              <div className="flag">
                                <img
                                  src={`${
                                    constants.API.COUNTRY.GET_FLAG_DOWNLOAD
                                  }${
                                    item.flag_Upload_Id
                                      ? item.flag_Upload_Id
                                      : ""
                                  }`}
                                />
                              </div>

                              <div className="navigation">
                                {favouriteCountry &&
                                favouriteCountry.employeeCount > 0 ? (
                                  <div
                                    onClick={() =>
                                      handleChangeMap(mapData, item.id)
                                    }
                                  >
                                    <div
                                      className={`${
                                        mapData.includes(item.id)
                                          ? "pin_active"
                                          : "pin_normal"
                                      }`}
                                    >
                                      <i className="ph-push-pin-fill"></i>
                                    </div>
                                  </div>
                                ) : (
                                  <div
                                    onClick={() =>
                                      handleChangeMap(mapData, item.id)
                                    }
                                  >
                                    <div
                                      className={`${
                                        mapData.includes(item.id)
                                          ? "pin_active"
                                          : "pin_normal"
                                      }`}
                                    >
                                      <i className="ph-push-pin-fill" />
                                    </div>
                                  </div>
                                )}
                              </div>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </>
              ) : (
                <>
                  <div className="row">
                    <div className="country-wrapper">
                      <div className="row">
                        {specificRegionList &&
                          specificRegionList.map((region) => {
                            return (
                              <div className="country-list">
                                <a href="#">
                                  <span className="country-list-active">
                                    {region.country_Name}
                                  </span>
                                </a>
                                <div className="flag">
                                  <img src={IndiaFlag} />
                                </div>
                                <a href="#">
                                  <div className="navigation">
                                    <div
                                      onClick={() =>
                                        handleChangeMap(mapData, region.id)
                                      }
                                    >
                                      <div
                                        className={`${
                                          mapData.includes(region.id)
                                            ? "pin_active"
                                            : "pin_normal"
                                        }`}
                                      >
                                        <i className="ph-push-pin-fill" />
                                      </div>
                                    </div>
                                  </div>
                                </a>
                              </div>
                            );
                          })}
                      </div>
                    </div>
                  </div>
                </>
              )}
            </div>
            <div className="col-12">
              <div className="action-wrap">
                <div className="row align-items-center justify-content-between">
                  <div>
                    <Link
                      to={constants.ROUTE.LOCATION.LOCATION}
                      className="skipbtn"
                    >
                      Skip for now
                    </Link>
                  </div>
                  <div>
                    <button
                      type="button"
                      className="btn btn-primary"
                      onClick={handleSubmit}
                    >
                      Next
                      <i className="ph-arrow-right" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    timeline: state.timeline,
  };
};
const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      countrySelectionAction: countrySelectionAction,
      callgetCountryListAPIAction: callgetCountryListAPI,
      callgetRegionListAPIAction: callgetRegionListAPI,
      callgetSpecificRegionListAPIAction: callgetSpecificRegionListAPI,
      callgetFavouriteCountryAPIAction: callgetFavouriteCountryAPI,
      callAddCountryAPIAction: callAddCountryAPI,
    },
    dispatch
  );

export default connect(mapStateToProps, mapDispatchToProps)(Country);
