import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import constants from "../../utils/constants";
import helpCircle from "../../assets/images/help-circle.svg";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  callAddCoworkers,
  callgetCoworkersList,
  callgetUserByKeyClockId,
} from "../../Actions/AddUserAction";
import { CustomeNotification } from "../../Components/CustomeNotification/CustomeNotification";
import {
  getKeyClockToken_Data,
  storeKeyClockUserId,
} from "../../utils/storage/index";
import jwt_decode from "jwt-decode";
import UserRegistrationSidebar from "../../Components/UserRegistrationLayout/UserRegistrationSidebar";
import TextFieldComponent from "../../Components/TextFieldComponent/TextFieldComponent";
import { addUserAction } from "../../ReducerAction/TimelineAction";

const AddUser = (props) => {
  const history = useHistory();
  const [loggedInUserId, setloggedInUserId] = useState("");
  const [id, setID] = useState("");
  const [initialValues, setInitialValues] = useState({
    FirstName: "",
    emailid: "",
    LastName: "",
  });
  const [coWorkerEmail, setcoWorkerEmail] = useState([]);
  const [coWorkerLength, setcoWorkerLength] = useState();
  console.log(coWorkerLength);

  useEffect(() => {
    var token_data = getKeyClockToken_Data();
    var newData = token_data ? jwt_decode(token_data) : "";
    var id = newData ? (newData.sub ? newData.sub : "") : "";
    console.log("newData", token_data);
    setID(id);
    getUserByKeyClock(id);
    // if(loggedInUserId){
    //   getCoworkers(loggedInUserId);
    // }
  }, []);
  useEffect(() => {
    if (loggedInUserId) {
      getCoworkers(loggedInUserId);
    }
  }, []);

  const request = {
    createdby: loggedInUserId,
    FirstName: initialValues.FirstName,
    LastName: initialValues.LastName,
    emailid: initialValues.emailid,
  };
  const handleChangeValue = (e) => {
    setInitialValues({
      ...initialValues,
      [e.target.name]: e.target.value,
    });
  };

  const getUserByKeyClock = (id) => {
    props
      .callgetUserByKeyClockIdAPIAction(id)
      .then((res) => {
        console.log("KeyClockId", res.data.data.id);
        setloggedInUserId(res.data.data.id);
        storeKeyClockUserId(res.data.data.id);
        getCoworkers(res.data.data.id);
      })
      .catch((error) => {});
  };

  const getCoworkers = (loggedInUserId) => {
    props
      .callgetCoworkersListAPIActon(loggedInUserId)
      .then((res) => {
        setcoWorkerEmail(res.data.data);
        console.log("response", res.data.data);
        console.log("response length", res.data.data.length);
        setcoWorkerLength(res.data.data.length);
      })
      .catch((error) => {});
  };

  const handleSubmit = () => {
    history.push(constants.ROUTE.SUCCESS.SUCCESS);
    props.addUserAction(true);
  };

  const handleAdd = (e) => {
    e.preventDefault();
    props
      .callPostAddCoworkerAPIAction(request)
      .then((res) => {
        console.log(res);
        CustomeNotification(
          "success",
          "User Added Successfully",
          "Success",
          2000
        );
        getCoworkers(loggedInUserId);
      })
      .catch((error) => {
        CustomeNotification(
          "error",
          error.response.data.errors,
          "Error",
          2500,
          () => {}
        );
      });
  };

  return (
    <div className="main_wrapper position-relative">
      <UserRegistrationSidebar />
      <div className="container">
        <div className="row justify-content-center mt-3">
          <div className="col-8">
            <div className="row">
              <div className="col-6">
                <a href className="back-btn">
                  Previous Step
                </a>
              </div>
            </div>
            <div className="col-12">
              <div className="row">
                <div className="header-section">
                  <h2>Add Users</h2>
                  <h4>
                    Invite up to four coworkers to explore Expandopedia with
                    you.
                  </h4>
                </div>
              </div>
              <div className="row">
                <div className="col-12 px-0">
                  <form>
                    <div className="col-12 px-0">
                      <div className="row">
                        <div className="col-6">
                          <div className="form-group">
                            <TextFieldComponent
                              type="text"
                              id="FirstName"
                              name="FirstName"
                              value={initialValues.FirstName}
                              onChange={handleChangeValue}
                              placeholder="First Name"
                              label="First Name"
                              dataContent="First Name"
                            />
                          </div>
                        </div>
                        <div className="col-6">
                          <div className="form-group">
                            <TextFieldComponent
                              type="text"
                              id="LastName"
                              name="LastName"
                              value={initialValues.LastName}
                              onChange={handleChangeValue}
                              placeholder="Last Name"
                              label="Last Name"
                              dataContent="Last Name"
                            />
                          </div>
                        </div>
                      </div>
                      <div className="form-group">
                        <TextFieldComponent
                          type="email"
                          id="emailid"
                          name="emailid"
                          value={initialValues.emailid}
                          onChange={handleChangeValue}
                          placeholder="Email Id"
                          label="Email Id"
                          dataContent="Email Id"
                        />
                      </div>
                      <div className="form-check">
                        <input
                          className="form-check-input"
                          type="checkbox"
                          defaultValue
                          id="defaultCheck1"
                        />
                        <label
                          className="form-check-label mr-3"
                          htmlFor="defaultCheck1"
                        >
                          User has admin privileges
                        </label>
                        <img src={helpCircle} />

                        <a href className="info-wrap">
                          <div className="info-content">
                            <h4>What’s this?</h4>
                            <p>
                              Admin users can add and manage unto four users.
                            </p>
                          </div>
                        </a>
                      </div>
                      <div className="form-check">
                        <input
                          className="form-check-input"
                          type="checkbox"
                          defaultValue
                          id="defaultCheck1"
                        />
                        <label
                          className="form-check-label mr-3"
                          htmlFor="defaultCheck1"
                        >
                          User has has access to purchased and downloaded
                          documents.
                        </label>
                      </div>
                    </div>
                  </form>
                  <div className="action-wrap">
                    <div className="row">
                      <div className="col d-flex justify-content-end pr-0">
                        <button
                          type="button"
                          // disabled={coWorkerLength >= 4}
                          className="btn btn-secondary add-btn"
                          onClick={handleAdd}
                        >
                          Add
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="row d-block">
                <h3 className="user-header">Users added</h3>
                <div className="user-list">
                  {coWorkerEmail ? (
                    coWorkerEmail.map((item, index) => {
                      return (
                        <a key={index} href className="odd">
                          {item.emailId}
                          <i className="ph-x" />
                        </a>
                      );
                    })
                  ) : (
                    <span>
                      Add users to see their email address listed here.
                    </span>
                   )} 
                  {coWorkerLength >= 4 ? (
                    <div className="error-text">
                      <h5>We were unable to invite some users.</h5>
                      <h6>
                        Try changing the email address or remove user to
                        proceed.{" "}
                      </h6>
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </div>
              <div className="action-wrap">
                <div className="row">
                  <div className="col pl-0">
                    <a href className="skipbtn">
                      Skip for now
                    </a>
                  </div>
                  <div className="col d-flex justify-content-end pr-0">
                    <button
                      type="button"
                      className="btn btn-primary"
                      onClick={handleSubmit}
                    >
                      Finish
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      addUserAction: addUserAction,
      callPostAddCoworkerAPIAction: callAddCoworkers,
      callgetCoworkersListAPIActon: callgetCoworkersList,
      callgetUserByKeyClockIdAPIAction: callgetUserByKeyClockId,
    },
    dispatch
  );
export default connect(null, mapDispatchToProps)(AddUser);
