import React, { useEffect, useState } from "react";
import {
  callgetAlertsAPI,
  callgetBlogArticleWPAPI,
  callgetBlogsforCountryAPI,
  callGetExpertDetailsAPI,
  callGetSolutionsAPI,
  callGetSuperTopicsAPI,
} from "../../Actions/HomeAction";
import profile_img from "../../Scss_Homepages/images/dp.jpeg";
import country_img from "../../Scss_Homepages/images/Hero-Image-askan.jpg";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import moment from "moment";
import OverlayTrigger from "react-bootstrap/OverlayTrigger"
import { Tooltip } from "react-bootstrap"
import "./_home.scss"
import ContentLoader from "react-content-loader"
import constants from "../../utils/constants";


const severityTypes = {
  CRITICAL: {
    name: "Critical",
    color: "border-maroon",
    bullet: "maroon_bg"
  },
  MAJOR: {
    name: "Major",
    color: "border-yellow",
    bullet: "yellow_bg"
  },
  MINOR: {
    name: "Minor",
    color: "border_blue",
    bullet: "blue_bg"
  },
  OTHER: {
    name: "other",
    color: "border_blue",
    bullet: "blue_bg"
  }
}

const Card = (props) => {
  const [alertData, setAlertData] = useState([]);
  const [blogData, setBlogData] = useState([]);
  const [expertDetails, setExpertDetails] = useState();
  const [alertLoading, setAlertLoading] = useState(true)
  const [blogLoading, setBlogLoading] = useState(true)
  const [expertLoading, setExpertLoading] = useState(true)

  useEffect(async () => {
    let alerts = await getAlerts()
    setAlertData(alerts?.data?.data || [])
    setAlertLoading(false)
  }, [])

  useEffect(async () => {
    if (props.expertSolutions.length) {
      let experts = await getExperts()
      setExpertDetails(experts && experts.length ? experts[0] : null)
      setExpertLoading(false)
    }
  }, [props.expertSolutions])

  useEffect(async () => {
    if (props.blogSolutions.length) {
      let blogs = await getBlogs()
      setBlogData(blogs || [])
      setBlogLoading(false)
    }
  }, [props.blogSolutions])

  const getAlerts = async () => {
    let alerts = await props.callgetAlertsAPIAction(props.country.id, props.blogCount)
    return alerts
  }

  const getBlogs = async () => {
    let data = props.blogSolutions
    if (data.length) {
      let solutionId = data[0].solutionId
      let blogs = await props.callgetBlogsforCountryAPIAction(solutionId, [props.name])
      let blogsArray = blogs?.data?.data
      if (blogsArray.length) {
        return blogsArray.slice(0, 2)
      }
    }
  }

  const getExperts = async () => {
    try {
      let data = props.expertSolutions
      if (data.length) {
        let solutionId = data[0].solutionId
        let experts = await props.callGetSuperTopicsAPIAction({
          SolutionIds: [solutionId],
          Tags: [props.name],
          ShowTopicHierarchy: true, 
        })
        return experts?.data?.data || []
      }
    }
    catch (err) {
      return
    }
  }

  const getClassNameBySeverity = () => {
    if (alertData.find(item => item.severityType === severityTypes.CRITICAL.name)) {
      return severityTypes.CRITICAL.color
    }
    if (alertData.find(item => item.severityType === severityTypes.MAJOR.name)) {
      return severityTypes.MAJOR.color
    }
    if (alertData.find(item => item.severityType === severityTypes.MINOR.name)) {
      return severityTypes.MINOR.color
    }
    return severityTypes.OTHER.color
  }

  const getSeverity = (type) => {
    let severity
    for (let key in severityTypes) {
      if (severityTypes[key].name === type) severity = severityTypes[key]
    }
    if (severity) return severity
    else return severityTypes.OTHER
  }

  const getExpertImage = (expert) => {
    let imageContent = expert?.topics?.length ? 
      expert.topics.find(item => item.contentTypeName === "Image")
      : null

    return imageContent?.topicContent
  }

  return (
    <>
      <div className="col-xl-4 col-lg-6">
        <div className={`country-card border-top ${getClassNameBySeverity()}`}>
          <div className="country-image-wrapper">
            <h3>{props.name}</h3>
          </div>
          <div className="country-image">
            <img src={country_img} />
          </div>
          {
            alertLoading ?
              <Loader />
              : alertData.length ?
                <>
                  {alertData.map((alert, index) => (
                    <div key={alert.alertId} className={`details-wrap ${index !== 0 ? "mt-4" : ""}`}>
                      <span>
                        {alert.changeCategory}
                        <div
                          className={`round ${getSeverity(alert.severityType).bullet}`}
                        />
                        <div className="date-ctrl">
                          {moment(alert.effectiveDate).format("MMM DD, YYYY")}
                        </div>
                      </span>
                      <span className="maintitle mt-1 mb-1 pr-0 ellipsis">
                        {alert.alertTitle}
                      </span>
                    </div>
                  ))}
                </>
                :
                <div className="details-wrap nodata">
                  <span>No new alerts to show</span>
                  <span>Last updated {moment().format("MMM DD, YYYY")}</span>
                </div>
          }
          <hr className="mt-4 mb-4" />
          {
            blogLoading ?
              <>
                <Loader />
                <hr className="mt-4 mb-4" />
              </>
              : blogData && blogData.length ?
                <>
                  {blogData.map((blog, index) => (
                    <div key={blog.supertopicId} className={`details-wrap ${index !== 0 ? "mt-4" : ""}`}>
                      <span>
                        Blog
                  <div className="date-ctrl">
                          {moment(blog.createdAt).format("MMM DD, YYYY")}
                        </div>
                      </span>
                      <OverlayTrigger
                        overlay={(props) => (
                          <Tooltip id={`button-tooltip-${index}`} {...props}>
                            {blog.supertopicName}
                          </Tooltip>
                        )}
                      >
                        <span className="maintitle mt-1 mb-1 pr-0 ellipsis">
                          {blog.supertopicName}
                        </span>
                      </OverlayTrigger>
                    </div>
                  ))}
                  <hr className="mt-4 mb-4" />
                </>
                : null}
          {expertLoading ?
            <div className="details-wrap">
              <Loader />
            </div>
            :
            <div className="author-details">
              <h3>Regional Expert Team Lead</h3>
              <h4>
                {expertDetails?.supertopicName ? expertDetails?.supertopicName : <>&nbsp;</>}
              </h4>
              <a href="#">Ask a Question</a>
              <div className="profile-pic">
                <img src={getExpertImage(expertDetails) ? `${constants.IMAGE.DOWNLOAD}${getExpertImage(expertDetails)}` : profile_img } />
              </div>
            </div>
          }
        </div>
      </div>
    </>
  );
};

const Loader = (props) => (
  <ContentLoader
    speed={2}
    width="100%"
    height={88}
    viewBox="0 0 100% 38"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
    {...props}
  >
    <rect x="0" y="0" rx="2" ry="2" width="100%" height="88" />
  </ContentLoader>
)

export const CardLoader = (props) => (
  <div className="col-xl-4 col-lg-6">
    <div className={`country-card border-top ${severityTypes.OTHER.color}`}>
      <div className="country-image-wrapper">
        <h3>{props.name}</h3>
      </div>
      <div className="country-image">
        <img src={country_img} />
      </div>
      <div className="details-wrap">
        <Loader />
      </div>
      <hr className="mt-4 mb-4" />
      <div className="details-wrap">
        <Loader />
      </div>
      <hr className="mt-4 mb-4" />
      <div className="details-wrap">
        <Loader />
      </div>
    </div>
  </div>

)

const mapDispatchToProps = (dispatch) =>
  bindActionCreators(
    {
      callgetBlogsforCountryAPIAction: callgetBlogsforCountryAPI,
      callgetAlertsAPIAction: callgetAlertsAPI,
      callGetExpertDetailsAPIAction: callGetExpertDetailsAPI,
      callgetBlogArticleWPAPIAction: callgetBlogArticleWPAPI,
      callGetSolutionsAPIAction: callGetSolutionsAPI,
      callGetSuperTopicsAPIAction: callGetSuperTopicsAPI
    },
    dispatch
  );

export default connect(null, mapDispatchToProps)(Card);
